import React from "react";

interface Props {
    condition: string;
}

export default function WeatherBackground({ condition }: Props) {
    const cond = condition.toLowerCase();

    // Determine effect type
    const isRain = cond.includes("rain") || cond.includes("drizzle") || cond.includes("shower");
    const isSnow = cond.includes("snow") || cond.includes("ice") || cond.includes("freezing") || cond.includes("blizzard");
    const isCloudy = cond.includes("cloud") || cond.includes("overcast") || cond.includes("fog") || cond.includes("mist") || cond.includes("haze");
    const isSunny = cond.includes("sun") || cond.includes("clear");
    const isThunder = cond.includes("thunder");

    return (
        <div className="absolute inset-0 overflow-hidden pointer-events-none z-0">
            {/* Sunny / Clear Effect */}
            {isSunny && (
                <div className="absolute top-[-100px] right-[-100px] w-[500px] h-[500px] bg-amber-100/40 rounded-full blur-[120px] animate-pulse" />
            )}

            {/* Rain Effect */}
            {isRain && (
                <div className="absolute inset-0 opacity-30">
                    {[...Array(30)].map((_, i) => (
                        <div
                            key={i}
                            className="absolute bg-blue-400 w-[1px] h-[40px]"
                            style={{
                                left: `${Math.random() * 100}%`,
                                top: `-50px`,
                                animation: `fall ${0.5 + Math.random() * 0.4}s linear infinite`,
                                animationDelay: `${Math.random() * 2}s`
                            }}
                        />
                    ))}
                </div>
            )}

            {/* Snow Effect */}
            {isSnow && (
                <div className="absolute inset-0 opacity-60">
                    {[...Array(40)].map((_, i) => (
                        <div
                            key={i}
                            className="absolute bg-white rounded-full"
                            style={{
                                left: `${Math.random() * 100}%`,
                                top: `-20px`,
                                width: `${3 + Math.random() * 5}px`,
                                height: `${3 + Math.random() * 5}px`,
                                filter: 'blur(1px)',
                                animation: `snow ${4 + Math.random() * 5}s linear infinite`,
                                animationDelay: `${Math.random() * 5}s`
                            }}
                        />
                    ))}
                </div>
            )}

            {/* Cloudy / Fog Effect */}
            {isCloudy && (
                <div className="absolute inset-0 opacity-20">
                    <div
                        className="absolute top-0 left-[-100px] w-[400%] h-full flex gap-32"
                        style={{ animation: 'drift 60s linear infinite' }}
                    >
                        {[...Array(5)].map((_, i) => (
                            <div key={i} className="w-[500px] h-[400px] bg-slate-200 rounded-full blur-[100px]" />
                        ))}
                    </div>
                </div>
            )}

            {/* Thunder Flash */}
            {isThunder && (
                <div
                    className="absolute inset-0 bg-white"
                    style={{ animation: 'flash 10s infinite' }}
                />
            )}
        </div>
    );
}
